



































import axios from 'axios';
import Vue from 'vue'
import SourceDetail from './detail/SourceDetail.vue';
export default Vue.extend({
    components:{
        SourceDetail,
    },
    data:function () {
        return {
            sources: [],
            fields:["Name","Version","Tag"],
            selSource:null
        }
    },
    mounted(){
        this.getData();
    },
    methods:{
        select(i:any){
            console.log("source")
            this.selSource = i;
        },
        async getData() {
            try {
                const response = await axios.get(`http://llogger.hopfenspace.org/api/Source/All`);
                this.sources = response.data;
            } catch (e) {
                console.log(e);
            }
        },
        newSource(){
            this.selSource =  {
                Name:'',
                Tag:'',
                Version:'',
                Secret:''
            };
        }
    }
})
